import classNames from 'classnames';
import React from 'react';
import { TemplateStatus } from '../../../types/template';

interface TemplateStatusBadgeProps {
    status: TemplateStatus;
    className?: string;
}

export const TemplateStatusBadge: React.FC<TemplateStatusBadgeProps> = ({ status, className }) => {
    const getStatusClasses = () => {
        switch (status) {
            case 'approved':
                return 'bg-green-100 text-green-800 border-green-200';
            case 'pending':
                return 'bg-yellow-100 text-yellow-800 border-yellow-200';
            case 'rejected':
                return 'bg-red-100 text-red-800 border-red-200';
            case 'draft':
                return 'bg-gray-100 text-gray-800 border-gray-200';
            default:
                return 'bg-gray-100 text-gray-800 border-gray-200';
        }
    };

    return (
        <span className={classNames('inline-flex px-2 py-1 text-xs font-semibold rounded border uppercase', getStatusClasses(), className)}>
            {status}
        </span>
    );
};
